import cTitle from 'components/title';
import { MessageBox } from 'mint-ui';
export default {
    data() {
        return {
            
            form:
            {
                refund_id:'',
                express_sn:'',
                express_company:''
              
            },
           
            express: []
        };
    },
    methods: {
        // change(value){
        //     console.log(value);
        //     this.form.express_code = value.code;
        //     this.express_company_name = value.name;
        // },
        getExpressInfo() {
            var that = this;
            $http.get('dispatch.express-list').then(function (response) {

                if (response.result == 1) {


                    console.log(response.data);
                    var myData = response.data;
                    that.express = myData.express_companies;

                }
            }, function (response) {
                // error callback
            });
        },
        submitInfo()
        {
             var that = this;

      $http.post('refund.operation.send', this.form, '正在提交申请').then(function (response) {
        if (response.result == 1) {

          MessageBox.alert(response.msg, '');
          that.$router.go(-1);

        } else {
          MessageBox.alert(response.msg);
        }
      }, function (response) {
        // error callback
      });

        }

    },
    activated() {
        this.form.refund_id = this.$route.params.refund_id;
        this.getExpressInfo();

    },
    components: { cTitle }
};


